<template>
    <div>
        <TopNav title="添加产品"></TopNav>
        <div class="wyu-main">
            <div class="wyu-block" v-loading="loading">
                <el-steps :active="active" simple>
                    <el-step title="基本信息"></el-step>
                    <el-step title="商品模型"></el-step>
                </el-steps>
                <el-form v-if="active == 1" :rules="rules" :model="goodsDetail" ref="goodsInfo" size="mini"
                         label-width="100px">
                    <div class="wyu-title">
                        <h2>基本信息</h2>
                    </div>
                    <el-form-item label="商品名称" prop="goods_name">
                        <el-input v-model="goodsDetail.goods_name" style="width: 500px;"></el-input>
                    </el-form-item>
                    <el-form-item label="商品编号" prop="goods_sn">
                        <el-input v-model="goodsDetail.goods_sn" style="width: 500px"></el-input>
                        <p class="wyu-form-tips">不填写自动生成</p>
                    </el-form-item>
                    <el-form-item label="商品分类" prop="cat_id">
                        <product-category @selCat="getCatId" :cat-id="goodsDetail.cat_id"
                                          :p-cat-id="goodsDetail.p_cat_id"></product-category>
                    </el-form-item>
                    <el-form-item label="商品缩略图" prop="photo">
                        <img-uploads :img="goodsDetail.photo" v-model="goodsDetail.photo" :w="100"
                                     h="100"></img-uploads>
                        <p class="wyu-form-tips">建议尺寸 120 X 120</p>
                    </el-form-item>
                    <el-form-item label="商品关键词">
                        <el-input v-model="goodsDetail.keywords" style="width: 500px"></el-input>
                        <p class="wyu-form-tips">搜索的重要 组成部分 最好以 “,” 隔开</p>
                    </el-form-item>
                    <div class="wyu-title">
                        <h2>扩展信息</h2>
                    </div>
                    <el-form-item label="上/下架">
                        <el-switch v-model="goodsDetail.is_online" :active-value="1" :inactive-value="0"></el-switch>
                    </el-form-item>
                    <el-form-item label="商品已售">
                        <el-input v-model="goodsDetail.sales_sum" style="width: 500px;"></el-input>
                        <p class="wyu-form-tips">用于 已售展示数量</p>
                    </el-form-item>
                    <el-form-item label="商品排序">
                        <el-input v-model="goodsDetail.orderby" style="width: 500px;"></el-input>
                        <p class="wyu-form-tips">数字越大越靠前</p>
                    </el-form-item>
                    <el-form-item label="商品浏览">
                        <el-input v-model="goodsDetail.views" style="width: 500px;"></el-input>
                        <p class="wyu-form-tips">数字越大越靠前</p>
                    </el-form-item>
                    <el-form-item label="商品推荐">
                        <el-checkbox-button v-model="goodsDetail.is_recommend" label="是否推荐" :true-label="1"
                                            :false-label="0"></el-checkbox-button>
                        <el-checkbox-button v-model="goodsDetail.is_new" label="是否新品" :true-label="1"
                                            :false-label="0"></el-checkbox-button>
                        <el-checkbox-button v-model="goodsDetail.is_hot" label="是否热卖" :true-label="1"
                                            :false-label="0"></el-checkbox-button>
                    </el-form-item>

                    <div class="wyu-title">
                        <h2>SEO设置</h2>
                    </div>
                    <el-form-item label="百度SEO标题" label-width="120px">
                        <el-input v-model="goodsDetail.seo.title" style="width:500px;"  autocomplete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="百度SEO描述" label-width="120px">
                        <el-input type="textarea" :autosize="{minRows: 2, maxRows: 5}" style="width:500px;" autocomplete="off"  placeholder="请输入内容" v-model="goodsDetail.seo.keywords"></el-input>
                    </el-form-item>
                    <el-form-item label="百度SEO关键词" label-width="120px">
                        <el-input type="textarea" :autosize="{minRows: 5, maxRows: 10}" style="width:500px;" autocomplete="off"  placeholder="请输入内容" v-model="goodsDetail.seo.description"></el-input>
                    </el-form-item>
                    <el-form-item label="百度SEO原始发布时间" label-width="120px">
                        <el-date-picker v-model="goodsDetail.seo.releaseDate" type="date" placeholder="选择日期"></el-date-picker>
                    </el-form-item>
                    <el-form-item label="百度封面图片" label-width="120px">
                        <img-uploads :multi="true" :img="goodsDetail.seo.image"  v-model="goodsDetail.seo.image"></img-uploads>
                        <p class="tit"> 用于信息流投放后的封面显示，最多3张，单图片最大2M；封面图建议尺寸：高>=210px 宽>=375px;最小尺寸: 高>=146px 宽>=218px</p>
                    </el-form-item>

                    <div class="wyu-title">
                        <h2>商品物流</h2>
                    </div>
                    <el-form-item label="是否包邮">
                        <el-radio-group size="mini" v-model="goodsDetail.is_free_shipping">
                            <el-radio :label="1">包邮</el-radio>
                            <el-radio :label="0">不包邮</el-radio>
                        </el-radio-group>
                    </el-form-item>
                    <el-form-item label="运费模板" v-if="goodsDetail.is_free_shipping == 0" prop="freight_template_id">
                            <select-freight :template-id="goodsDetail.freight_template_id" v-model="goodsDetail.freight_template_id"></select-freight>
                    </el-form-item>
                    <el-form-item label="重量" v-if="goodsDetail.is_free_shipping == 0" prop="weight">
                             <el-input style="width: 500px" v-model="goodsDetail.weight"></el-input>
                        <p class="wyu-form-tips">商品重量：单位 克</p>
                    </el-form-item>
                    <div class="wyu-title">
                        <h2>商品详情</h2>
                    </div>
                    <el-form-item label="商品相册" prop="photos">
                        <img-uploads :multi="true" :img="goodsDetail.photos" v-model="goodsDetail.photos" :w="100"
                                     :h="100"></img-uploads>
                        <p class="wyu-form-tips">建议尺寸 375 X 375</p>
                    </el-form-item>
                    <el-form-item label="商品详情" prop="goods_content">
                        <html-editor v-model="goodsDetail.goods_content" :html="goodsDetail.goods_content"
                                     title="商品详情"></html-editor>
                    </el-form-item>
                </el-form>
                <el-form v-if="active == 2" :rules="rules" :model="goodsDetail" ref="goodsprice" size="mini"
                         label-width="100px">
                    <div class="wyu-tips wyu-tips-warning">
                        <p> 1、 开启会员优惠 则必须填写会员优惠价格，则表示没有会员优惠</p>
                        <p> 2、 商品属性 填写则展示 不填写则不展示 ，商品模型 与 是否有规格 不可修改 请认证填写信息</p>
                        <p> 3、 开启分佣 则必须填写 分佣价格，关闭分佣 则视为 不参与分佣商品</p>
                    </div>
                    <div class="wyu-title">
                        <h2>价格&属性</h2>
                    </div>
                    <el-form-item label="商品模型">
                        <select-type v-model="goodsDetail.type_id" :type-id="goodsDetail.type_id" style="width: 300px"
                                     size="mini"></select-type>
                    </el-form-item>
                    <el-form-item label="是否可用优惠券">
                        <el-radio-group size="mini" v-model="goodsDetail.is_coupon">
                            <el-radio :label="0">可用优惠券</el-radio>
                            <el-radio :label="1">不可用优惠券</el-radio>
                        </el-radio-group>
                    </el-form-item>
                    <el-form-item label="会员专享">
                        <el-radio-group size="mini" v-model="goodsDetail.is_plus_vip">
                            <el-radio :label="0">会员无专享</el-radio>
                            <el-radio :label="1">会员有专享</el-radio>
                        </el-radio-group>
                    </el-form-item>
                    <el-form-item label="规格">
                        <el-radio-group size="mini" v-model="goodsDetail.sku_type">
                            <el-radio :label="0">无规格</el-radio>
                            <el-radio :label="1">多规格</el-radio>
                        </el-radio-group>
                    </el-form-item>
                    <el-form-item label="参与分佣">
                        <el-radio-group size="mini" v-model="goodsDetail.is_commission">
                            <el-radio :label="0">关闭分佣</el-radio>
                            <el-radio :label="1">开启分佣</el-radio>
                        </el-radio-group>
                    </el-form-item>
                    <div class="wyu-title">
                        <h2>商品属性</h2>
                    </div>
                    <el-form-item label-width="10px">
                        <goods-attr :attr-data="goodsDetail.goods_attr" v-model="goodsDetail.goods_attr"
                                    :type-id="goodsDetail.type_id"></goods-attr>
                    </el-form-item>
                    <div class="wyu-title">
                        <h2>商品价格 <span style="font-size: 10px">（会员价格设置0员则表示没有会员优惠）</span></h2>
                    </div>
                    <el-form-item label-width="10px" v-if="goodsDetail.sku_type == 1" prop="checkGoodsPrice">
                        <goods-spec
                                :spec-data="goodsDetail.goods_spec"
                                :is-plus-vip="goodsDetail.is_plus_vip"
                                :is-commission="goodsDetail.is_commission"
                                v-model="goodsDetail.goods_spec"
                                :type-id="goodsDetail.type_id">
                        </goods-spec>
                    </el-form-item>
                    <div v-if="goodsDetail.sku_type == 0">
                        <el-form-item label="市场价">
                            <el-input style="width: 500px" v-model="goodsDetail.market_price"></el-input>
                        </el-form-item>
                        <el-form-item label="本店价">
                            <el-input style="width: 500px" v-model="goodsDetail.shop_price"></el-input>
                        </el-form-item>
                        <el-form-item label="会员价" v-if="goodsDetail.is_plus_vip == 1">
                            <el-input style="width: 500px" v-model="goodsDetail.plus_vip_price"></el-input>
                        </el-form-item>
                        <el-form-item label="库存">
                            <el-input style="width: 500px" v-model="goodsDetail.goods_num"></el-input>
                        </el-form-item>
                        <el-form-item label="佣金" v-if="goodsDetail.is_commission == 1" >
                            <el-input v-model="goodsDetail.commission"   style="width: 500px"></el-input>
                        </el-form-item>
                    </div>
                </el-form>
            </div>
        </div>
        <div class="wyu-btn-div">
            <el-button size="small" type="primary" v-if="active < 2" @click="stepActive()">下一步</el-button>
            <el-button size="small" type="primary" v-if="active == 2" @click="createGoods()" >创建商品</el-button>
            <el-button size="small" type="primary" v-if="active > 1" @click="active = active - 1">上一步</el-button>
            <router-link to="/product/product/index"  style="margin-left: 10px"><el-button size="small">取消</el-button></router-link>
        </div>
    </div>
</template>

<script>
    import TopNav from "@/components/TopNav";
    import * as api from "@/api";
    import ImgUploads from "../../../components/Photos";
    import HtmlEditor from "../../../components/HtmlEditor";
    import SelectType from "../type/components/SelectType";
    import GoodsSpec from "../components/GoodsSpec";
    import GoodsAttr from "../components/GoodsAttr";
    import ProductCategory from "../components/ProductCategory";
    import SelectFreight from "../components/SelectFreight";
    export default {
        name: 'GoodsCreate',
        components: {
            SelectFreight,
            ProductCategory, GoodsAttr, GoodsSpec, SelectType, HtmlEditor, ImgUploads, TopNav
        },
        data() {
            let checkCatSelect = (rule, value, callback) => {
                if (value <= 0) {
                    callback(new Error("请选择二级分类"));
                }
                callback();
            };
            let checkTmpId = (rule, value, callback) => {
                if(this.goodsDetail.is_free_shipping == 0 &&  value <= 0){
                    callback(new Error("请选择物流"));
                }
                callback();
            };
            let checkweight = (rule, value, callback) => {
                if(this.goodsDetail.is_free_shipping == 0 &&  value <= 0){
                    callback(new Error("请输入商品重量单位克"));
                }
                callback();
            };
            return {
                loading:false,
                active: 1,
                typeId: 0,
                formNameKey: ['goodsInfo', 'goodsprice', 'goodsArea'],
                goodsDetail: {
                    cat_id: 0,//分类ID
                    p_cat_id: 0,//为可视化部分冗余的 父级分类ID
                    goods_sn: '',//商品编号
                    goods_name: "", //商品名称
                    brand_id: 0,//品牌ID
                    goods_num: 0,//库存数量
                    comment_count: 0,//商品评论数量
                    weight: 0,//商品重量 单位克
                    market_price: 0,//市场价
                    shop_price: 0,//本店价
                    keywords: '',//商品关键词
                    goods_content: [],//商品详情
                    photo: '',//商品缩略图
                    photos: [],//商品相册
                    is_online: 1, //是否上架
                    is_free_shipping: 1,//是否包邮
                    sort: 0,//商品排序
                    is_recommend: 0,//是否推荐
                    is_new: 0,//是否是新品
                    is_hot: 0,//是否是热卖
                    give_integral: 0,//购买商品赠送积分
                    sales_sum: 0,//商品销量
                    commission: 0,//分佣提成
                    plus_vip_price: 0,//会员专享价格
                    goods_attr: [],//商品属性
                    goods_spec: {},//是否有佣金
                    sku_type: 1,//商品模型
                    type_id: 0,//商品模型
                    is_plus_vip: 0,//是否有会员优惠
                    is_commission: 0,//是否有佣金
                    is_coupon: 0,//是否有佣金
                    freight_template_id:0,
                    views:0,
                    seo: {
                        title: '',
                        keywords: '',
                        description: '',
                        releaseDate: '',
                        image: [],
                    },
                },
                //校验规则
                rules: {
                    goods_name: [
                        {required: true, message: '请输入商品名称', trigger: 'blur'},
                        {min: 1, max: 64, message: '长度在 1 到 64 个字符', trigger: 'blur'}
                    ],
                    freight_template_id: [
                        {validator: checkTmpId, trigger: 'change'}
                    ],
                    weight: [
                        {validator: checkweight, trigger: 'change'}
                    ],
                    cat_id: [
                        {validator: checkCatSelect, trigger: 'change'}
                    ],
                    photo: [
                        {required: true, message: '请上传缩略图', trigger: 'blur',}
                    ],
                    photos: [
                        {type: 'array', required: true, message: '至少上传一张相册', trigger: 'blur'}
                    ],
                    goods_content: [
                        {type: 'array', required: true, message: '请编辑详情', trigger: 'blur'}
                    ],
                }
            }
        },

        created() {
            if (sessionStorage.getItem("goodsDetail")) {
                let detail = JSON.parse(sessionStorage.getItem("goodsDetail"));
                this.goodsDetail = detail;
                this.goodsDetail.goods_content = detail.goods_content;
            }
        },
        methods: {

            //验证商品模型
            checkGoodsType() {
                if (this.goodsDetail.type_id <= 0) {
                    return false;
                }
                return true;
            },
            //验证商品价格
            checkGoodsPrice() {
                if (this.goodsDetail.sku_type == 1) {
                    let returnBol = true;
                    this.goodsDetail.goods_spec.itemPrice.some((val) => {
                        if (val.invalid == 0) {
                            if (val.market_price <= 0 || val.goods_num <= 0 || val.weight <= 0) {
                                returnBol = false;
                                return false;
                            }
                            if (this.goodsDetail.is_plus_vip == 1 && val.plus_vip_price <= 0) {
                                returnBol = false;
                                return false;
                            }
                            if (this.goodsDetail.is_commission == 1 && val.commission <= 0) {
                                returnBol = false;
                                return false;
                            }
                        }
                    })
                    return returnBol;
                } else {
                    if (this.goodsDetail.market_price <= 0 || this.goodsDetail.goods_num <= 0 || this.goodsDetail.weight <= 0) {
                        return false;
                    }
                    if (this.goodsDetail.is_plus_vip == 1 && this.goodsDetail.plus_vip_price <= 0) {
                        return false;
                    }
                    if (this.goodsDetail.is_commission == 1 && this.goodsDetail.commission <= 0) {
                        return false;
                    }
                }
                return true;
            },
            //设置商品缓存
            setGoodsCash(clear = false) {
                if (clear) {
                    sessionStorage.setItem("goodsDetail", JSON.stringify(this.goodsDetail));
                } else {
                    sessionStorage.removeItem("goodsDetail");
                }
            },

            getCatId(val) {
                this.goodsDetail.p_cat_id = +val.p_cat_id;
                this.goodsDetail.cat_id = +val.cat_id;
            },
            //下一步操作
            stepActive() {
                this.setGoodsCash(true);
                this.$refs[this.formNameKey[this.active - 1]].validate((valid) => {
                    if (valid) {
                        if (this.active == 2) {
                            if (this.checkGoodsType() == false) {
                                this.$message({
                                    showClose: true,
                                    message: '请选择商品模型',
                                    type: 'error'
                                });
                                return false;
                            }
                            if (!this.checkGoodsPrice()) {
                                this.$message({
                                    showClose: true,
                                    message: '请把商品价格填写完整',
                                    type: 'error'
                                });
                                return false;
                            }
                        }
                        this.active = this.active + 1;
                    } else {
                        return false;
                    }
                });
            },
            //提交商品到商品库
            createGoods() {
                this.loading  = true;
                api.post("/shop/admin.goods.goods/create", {
                    appToken: this.$store.getters.appToken,
                    cat_id: this.goodsDetail.cat_id,  //分类ID
                    p_cat_id: this.goodsDetail.p_cat_id,  //为可视化部分冗余的 父级分类ID
                    goods_sn: this.goodsDetail.goods_sn,  //商品编号
                    goods_name: this.goodsDetail.goods_name,  //商品名称
                    brand_id: this.goodsDetail.brand_id,  //品牌ID
                    goods_num: this.goodsDetail.goods_num,  //库存数量
                    comment_count: this.goodsDetail.comment_count,  //商品评论数量
                    weight: this.goodsDetail.weight,  //商品重量 单位克
                    market_price: this.goodsDetail.market_price,  //市场价
                    shop_price: this.goodsDetail.shop_price,  //本店价
                    keywords: this.goodsDetail.keywords,  //商品关键词
                    goods_content: JSON.stringify(this.goodsDetail.goods_content),  //商品详情
                    photo: this.goodsDetail.photo,  //商品缩略图
                    photos: JSON.stringify(this.goodsDetail.photos), //商品相册
                    is_online: this.goodsDetail.is_online,  //是否上架
                    is_free_shipping: this.goodsDetail.is_free_shipping,  //是否包邮
                    orderby: this.goodsDetail.orderby,  //商品排序
                    is_recommend: this.goodsDetail.is_recommend,  //是否推荐
                    is_new: this.goodsDetail.is_new,  //是否是新品
                    is_hot: this.goodsDetail.is_hot,  //是否是热卖
                    give_integral: this.goodsDetail.give_integral,  //购买商品赠送积分
                    sales_sum: this.goodsDetail.sales_sum,  //商品销量
                    commission: this.goodsDetail.commission,  //分佣提成
                    shipping_area_ids: JSON.stringify(this.goodsDetail.shipping_area_ids),  //配送物流
                    plus_vip_price: this.goodsDetail.plus_vip_price,  //会员专享价格
                    goods_attr: JSON.stringify(this.goodsDetail.goods_attr),  //商品属性
                    goods_spec: JSON.stringify(this.goodsDetail.goods_spec),  //是否有佣金
                    sku_type: this.goodsDetail.sku_type,  //商品模型
                    type_id: this.goodsDetail.type_id,  //商品模型
                    is_plus_vip: this.goodsDetail.is_plus_vip,  //是否有会员优惠
                    is_commission: this.goodsDetail.is_commission,  //是否有佣金
                    is_coupon: this.goodsDetail.is_coupon,  //是否有佣金
                    freight_template_id: this.goodsDetail.freight_template_id,  //是否有佣金
                    views: this.goodsDetail.views,  //是否有佣金
                    seo: JSON.stringify(this.goodsDetail.seo),  //是否有佣金
                }).then(() => {
                    this.loading = false;
                    this.setGoodsCash(false);
                    this.$message({
                        message: '提交成功',
                        type: 'success'
                    });
                    setTimeout(()=>{
                        this.$router.push("/product/product/index");
                    },1000);
                }).catch(() => {
                    this.loading = false;
                });
            },
        }
    }
</script>
