<template>
    <div style="min-width: 950px" v-loading="loading">
        <el-form v-if="spec.length > 0" size="mini" label-width="80px" style="margin: 0;padding: 0">
            <el-form-item label="规格项">
                <el-form-item :label="val.name + '：'"    v-for="(val,key) in spec" :key="key" label-width="100px">
                    <el-checkbox-group v-model="checkSpec" @change="setCheckSpec">
                        <el-checkbox v-for="(v,k) in val.item" :label="v.item_id" :key="k">{{v.item}}</el-checkbox>
                    </el-checkbox-group>
                </el-form-item>
            </el-form-item>
            <el-form-item label="快捷操作">
                <el-form label-position="top" size="mini" style="margin: 0;padding: 0">
                    <el-col :span="3">
                        <el-form-item label="门市价">
                            <el-input v-model="setAllValue.market_price"></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="3">
                        <el-form-item label="本店价">
                            <el-input v-model="setAllValue.shop_price"></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="3">
                        <el-form-item label="库存">
                            <el-input v-model="setAllValue.goods_num"></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="3" v-if="isPlusVip == 1">
                        <el-form-item label="会员价">
                            <el-input v-model="setAllValue.plus_vip_price"></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="3" v-if="isCommission == 1">
                        <el-form-item label="推广佣金">
                            <el-input v-model="setAllValue.commission"></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="3">
                        <el-form-item label="操作">
                            <el-button @click="setAll">全部设置</el-button>
                        </el-form-item>
                    </el-col>
                </el-form>
            </el-form-item>
            <el-form-item label="规格设置">
                <table class="stock-table">
                    <thead>
                    <tr>
                        <th v-for="item in specification">{{item.name}}</th>
                        <th><b style="color: red">*</b>门市价</th>
                        <th><b style="color: red">*</b>本店价</th>
                        <th><b style="color: red">*</b>库存</th>
                        <th v-if="isPlusVip == 1"><b style="color: red"></b>会员价</th>
                        <th v-if="isCommission"><b style="color: red"></b>推广佣金</th>
                        <th>规格编号</th>
                        <th>缩略图</th>
                        <th>操作</th>
                    </tr>
                    </thead>
                    <tbody style="max-height: 10px">
                    <tr v-for="(item, index) in countSum(0)" :key="index">
                        <td v-for="(n, specIndex) in specification.length" style="text-align: center"
                            v-if="showTd(specIndex, index)"
                            :rowspan="countSum(n)">
                            {{getSpecAttr(specIndex, index)}}
                        </td>
                        <td>
                            <el-input @input="setSpecPrice" :disabled="values[index].invalid == 1" type="number" v-model="values[index].market_price" size="mini"></el-input>
                        </td>
                        <td>
                            <el-input @input="setSpecPrice" :disabled="values[index].invalid == 1" type="number" v-model="values[index].shop_price" size="mini"></el-input>
                        </td>
                        <td>
                            <el-input @input="setSpecPrice" :disabled="values[index].invalid == 1" type="number" v-model="values[index].goods_num" size="mini"></el-input>
                        </td>
                        <td v-if="isPlusVip == 1">
                            <el-input @input="setSpecPrice" :disabled="values[index].invalid == 1" type="number" v-model="values[index].plus_vip_price" size="mini"></el-input>
                        </td>
                        <td v-if="isCommission == 1">
                            <el-input @input="setSpecPrice" :disabled="values[index].invalid == 1" type="number" v-model="values[index].commission" size="mini"></el-input>
                        </td>
                        <td>
                            <el-input @input="setSpecPrice" :disabled="values[index].invalid == 1" v-model="values[index].bar_code" size="mini"></el-input>
                        </td>
                        <td style="width: 70px">
                            <div style="display: inline-flex;justify-content: center;width: 100%;margin-top: 10px">
                                <img-uploads :img="values[index].photo" v-model="values[index].photo" :w="50" :h="50"></img-uploads>
                            </div>
                        </td>
                        <td>
                            <div style="display: inline-flex;justify-content: center;width: 100%;">
                                <el-button
                                        :type="values[index].invalid ? 'info': ''"
                                        @click="setInvalid(index)"
                                        size="mini"
                                > {{values[index].invalid ? '有效' : '无效'}}
                                </el-button>
                            </div>
                        </td>
                    </tr>
                    </tbody>
                </table>
            </el-form-item>
        </el-form>
        <p v-if="spec.length <= 0" class="wyu-form-tips">当前模板没有规格项</p>
    </div>
</template>

<script>
    import * as api from "@/api";
    import ImgUploads from "../../../components/Photos";

    export default {
        name: "GoodsSpec",
        components: {ImgUploads},
        props: ['specData', 'typeId', 'isPlusVip', 'isCommission'],
        data() {
            return {
                loading: false,
                spec: [],
                img: '',
                datas: [],
                checkSpec: [],
                specification: [],
                values: [], // 用来存储要添加的规格属性
                setAllValue: {
                    market_price: 0, //市场价
                    shop_price: 0, //本店价格
                    plus_vip_price: 0, //会员价格
                    goods_num: 0, //库存
                    commission: 0, //佣金

                },
            }
        },

        watch: {
            typeId() {
                this.specification = [];
                this.checkSpec = [];
                this.values = [];
                this.spec = [];
                this.getSpec();
                this.checkSpec = this.specData.spec ? this.specData.spec : [];
            },
        },

        created() {
            this.checkSpec = this.specData.spec ? this.specData.spec : [];
            this.getSpec();
        },

        methods: {
            setInvalid(index) {
                this.values[index].invalid = this.values[index].invalid == 1 ? 0 : 1;
                this.setSpecPrice();
            },
            //全部设置
            setAll() {
                this.values.forEach(val => {
                    if (this.setAllValue.shop_price > 0) {
                        val.shop_price = this.setAllValue.shop_price;
                    }
                    if (this.setAllValue.goods_num > 0) {
                        val.goods_num = this.setAllValue.goods_num;
                    }
                    if (this.setAllValue.commission > 0) {
                        val.commission = this.setAllValue.commission;
                    }
                    if (this.setAllValue.plus_vip_price > 0) {
                        val.plus_vip_price = this.setAllValue.plus_vip_price;
                    }
                    if (this.setAllValue.market_price > 0) {
                        val.market_price = this.setAllValue.market_price;
                    }
                });
                this.setAllValue = {
                    market_price: 0, //市场价
                    shop_price: 0, //本店价格
                    plus_vip_price: 0, //会员价格
                    goods_num: 0, //库存
                    commission: 0, //库存
                };
                this.setSpecPrice();
            },
            //根据模型ID获取规格模型
            getSpec() {
                if (this.typeId) {
                    this.loading = true;
                    api.post("/shop/admin.spec/getSpecDetail", {
                        appToken: this.$store.getters.appToken,
                        type_id: this.typeId,
                    }).then(res => {
                        this.spec = res.detail;
                        this.loading = false;
                        this.setCheckSpec(this.checkSpec);
                        this.values = this.specData.itemPrice ? this.specData.itemPrice : [];
                    }).catch(() => {
                        this.loading = false;
                    });
                }
            },
            setSpecPrice() {
                this.$emit("input", {
                    type_id: this.typeId,
                    spec: this.checkSpec,
                    itemPrice: this.values,
                });
            },
            makeValues() {
                let num = this.countSum(0);
                let values = [];
                for (let i = 0; i < num; i++) {
                    values.push(this.getSpecKey(i));
                }
                this.values = values;
            },
            setCheckSpec(item_ids) {
                let newSpec = [];
                this.spec.forEach((val) => {
                    let specAdd = false;
                    let specItem = [];
                    val.item.forEach((v) => {
                        if (item_ids.indexOf(v.item_id) != -1) {
                            specItem.push(v);
                            specAdd = true;
                        }
                    });
                    if (specAdd) {
                        newSpec.push({
                            name: val.name,
                            value: specItem,
                        });
                    }
                });
                if (newSpec.length > 3) {
                    this.$message("您最多添加个三规格大类");
                    return false;
                }
                this.specification = newSpec;
                this.makeValues();
            },

            getSpecKey(index) {
                let count = this.specification.length;
                let spec_key = "";
                let key_name = "";
                for (let i = 0; i < count; i++) {
                    let num = this.countSum(i);
                    let item_index = parseInt((index % num) / (num / this.specification[i].value.length));
                    let name = this.specification[i].name;
                    spec_key += this.specification[i].value[item_index].item_id + "_"
                    key_name += name + ":" + this.specification[i].value[item_index].item + ","
                }
                return {
                    spec_key: spec_key.slice(0, -1),
                    key_name: key_name.slice(0, -1),
                    market_price: 0, //市场价
                    shop_price: 0, //本店价格
                    plus_vip_price: 0, //会员价格
                    goods_num: 0, //库存
                    commission: 0, //库存
                    bar_code: '', //商品编码
                    invalid: 0,
                    photo: "",
                };
            },

            getSpecAttr(specIndex, index) {
                const currentValues = this.specification[specIndex].value
                let indexCopy
                if (this.specification[specIndex + 1] && this.specification[specIndex + 1].value.length) {
                    indexCopy = index / this.countSum(specIndex + 1)
                } else {
                    indexCopy = index
                }
                const i = indexCopy % currentValues.length
                if (i.toString() !== 'NaN' && i % 1 === 0) {
                    return currentValues[i].item
                } else {
                    return ''
                }
            },


            countSum(specIndex) {
                if (this.specification.length <= 0) {
                    return 0;
                }
                let num = 1;
                this.specification.forEach((item, index) => {
                    if (index >= specIndex && item.value.length) {
                        num *= item.value.length
                    }
                });
                return num;
            },

            showTd(specIndex, index) {
                if (!this.specification[specIndex]) {
                    return false
                } else if (index % this.countSum(specIndex + 1) === 0) {
                    return true
                } else {
                    return false
                }
            },
        },
    }
</script>

<style scoped>
    .stock-table {
        width: 100%;
        color: #606266;
        max-height: 500px;
    }

    .no-stock {
        background: #909399;
    }

    .stock-table th {
        font-weight: unset;
    }

    .stock-table td, .stock-table th {
        /*padding: 5px 10px;*/
        border: 1px solid #ddd;
        width: 100px;
        /*font-size: 14px;*/
    }
</style>
