<template>
    <div v-loading="loading">
        <p v-if="attr.length > 0" class="wyu-form-tips">所有商品属性 都非必填项</p>
        <p v-if="attr.length <= 0" class="wyu-form-tips">当前模型没有属性</p>
        <el-form size="mini" label-width="80px" style="margin: 0;padding: 0;">
            <el-form-item v-for="(val,key) in attr" :label="val.attr_name">
                <el-input v-model="attrValue.attr[key].value" @input="setAttr" v-if="val.attr_input_type == 0"
                          style="width: 500px;"></el-input>
                <el-select v-model="attrValue.attr[key].value" @input="setAttr"  v-if="val.attr_input_type == 1" placeholder="请选择">
                    <el-option
                            v-for="item in val.attr_values"
                            :key="item"
                            :label="item"
                            :value="item">
                    </el-option>
                </el-select>
                <el-input v-model="attrValue.attr[key].value" @input="setAttr"  type="textarea" v-if="val.attr_input_type == 2"
                          style="width: 500px;"></el-input>
                <p class="wyu-form-tips">此项选填</p>
            </el-form-item>
        </el-form>
    </div>
</template>

<script>
    import * as api from "@/api";
    export default {
        name: "GoodsAttr",
        props: ['attrData', 'typeId'],
        data() {
            return {
                loading: false,
                attr: [],
                attrValue: {
                    type_id:0,
                    attr:[],
                },
            }
        },
        watch: {
            typeId() {
                this.attr = [];

                this.getAttr();
            },
        },
        created() {
            this.getAttr();
        },
        methods: {
            setAttr(){
                this.$emit("input",this.attrValue);
            },
            //根据typeId 获取 商品属性
            getAttr() {
                if (this.typeId) {
                    this.loading = true;
                    api.post("/shop/admin.attribute/getTypeAttr", {
                        appToken: this.$store.getters.appToken,
                        type_id: this.typeId,
                    }).then(res => {
                        this.loading = false;
                        this.attrValue.attr = [];
                        if(this.attrData.type_id == this.typeId && this.attrData.attr.length > 0){
                            this.attrValue  = this.attrData;
                        }else{
                            res.list.forEach((val) => {
                                this.attrValue.attr.push({
                                    "name": val.attr_name,
                                    "value": "",
                                });
                            });
                        }
                        this.attrValue.type_id = this.typeId;
                        this.attr = res.list;
                    }).catch(() => {
                        this.loading = false;
                    });
                }
            },
        },
    }
</script>

<style scoped>

</style>
